// var btoa    = require("btoa");
var Adapter = require("./adapter");

/**
 * Walks through an object (or array) and returns the value found at the
 * provided path. This function is very simple so it intentionally does not
 * support any argument polymorphism, meaning that the path can only be a
 * dot-separated string. If the path is invalid returns undefined.
 * @param {Object} obj The object (or Array) to walk through
 * @param {String} path The path (eg. "a.b.4.c")
 * @returns {*} Whatever is found in the path or undefined
 */
function getPath(obj, path = "") {
    path = path.trim();
    if (!path) {
        return obj;
    }
    return path.split(".").reduce(
        (out, key) => out ? out[key] : undefined,
        obj
    );
}

function setPath(obj, path, value) {
    path.trim().split(".").reduce(
        (out, key, idx, arr) => {
            if (out && idx === arr.length - 1) {
                out[key] = value;
            } else {
                return out ? out[key] : undefined;
            }
        },
        obj
    );
}

function makeArray(arg) {
    if (Array.isArray(arg)) {
        return arg;
    }
    return [arg];
}

function absolute(path, server) {
    if (path.match(/^http/)) return path;
    if (path.match(/^urn/)) return path;
    return server.serviceUrl.replace(/\/+$/, "") + "/" + path.replace(/^\/+/, "");
}

function HTTPError(message, statusCode, statusText) {
    Error.call(this, message);
    this.message    = message;
    this.name       = "HTTPError";
    this.statusCode = statusCode;
    this.status     = statusCode;
    this.statusText = statusText;
}

HTTPError.prototype = new Error;

HTTPError.prototype.toJSON = function() {
    return {
        name      : this.name,
        statusCode: this.statusCode,
        status    : this.status,
        statusText: this.statusText,
        message   : this.message
    };
};

HTTPError.create = failure => {
    // start with generic values
    var status     = 0;
    var statusText = "Error";
    var message    = "Unknown error";

    if (failure) {
        if (typeof failure == "object") {
            if (failure instanceof Error) {
                message = failure.message;
            }
            else if (failure.error) {
                status = failure.error.status || 0;
                statusText = failure.error.statusText || "Error";
                if (failure.error.responseText) {
                    message = failure.error.responseText;
                }
            }
        }
        else if (typeof failure == "string") {
            message = failure;
        }
    }
    // console.log("failure: ", failure)
    return new HTTPError(message, status, statusText);
};


function createAPI(fhir, options)
{
    const api = fhir(options, Adapter.get());

    [
        "conformance",
        // "document", "profile", "transaction", "history",
        // "typeHistory", "resourceHistory",
        "read",
        // "vread", "delete",
        // "create", "validate",
        // "search",
        // "update", "conditionalUpdate",
        // "nextPage", "prevPage", "getBundleByUrl", "resolve", "patch",
        // "fetchAll",
        // "fetchAllWithReferences"
    ].forEach(name => {
        api[name] = (function(orig) {
            return (...args) => orig.apply(api, args).then(
                ({ data }) => data,
                failure => {
                    throw HTTPError.create(failure);
                }
            );
        })(api[name]);
    });

    // for (const name in api) {
    //     if (api.hasOwnProperty(name) && typeof api[name] == "function") {
    //         api[name] = (function(orig) {
    //             return (...args) => orig.apply(api, args).then(
    //                 ({ data }) => data,
    //                 failure => {
    //                     throw HTTPError.create(failure);
    //                 }
    //             );
    //         })(api[name]);
    //     }
    // }
    return api;
}


module.exports = FhirClient;

function ClientPrototype(){}
var clientUtils = require("./utils");
Object.keys(clientUtils).forEach(function(k){
    ClientPrototype.prototype[k] = clientUtils[k];
});

/**
 * 
 * @param {Object} p
 * @param {String} p.serviceUrl 
 * @param {Object} p.auth 
 * @param {String} p.auth.type
 * @param {String} p.auth.username
 * @param {String} p.auth.password
 * @param {String} p.auth.token
 * @param {String} p.patientId
 * @param {String} p.userId
 */
function FhirClient(p) {

    if (typeof p == "string") {
        p = {
            serviceUrl: p
        };
    }
    // p.serviceUrl
    // p.auth {
    //    type: 'none' | 'basic' | 'bearer'
    //    basic --> username, password
    //    bearer --> token
    // }

    var client = new ClientPrototype();
    var fhir = Adapter.get().fhirjs;

    var server = client.server = {
        serviceUrl: p.serviceUrl,
        auth: p.auth || { type: "none" }
    };

    var auth = {};

    if (server.auth.type === "basic") {
        auth = {
            user: server.auth.username,
            pass: server.auth.password
        };
    } else if (server.auth.type === "bearer") {
        auth = {
            bearer: server.auth.token
        };
    }

    // client.api = fhir({
    //     baseUrl: server.serviceUrl,
    //     auth: auth
    // }, Adapter.get());
    client.api = createAPI(fhir, {
        baseUrl: server.serviceUrl,
        auth: auth
    });

    if (p.patientId) {
        client.patient = {};
        client.patient.id = p.patientId;
        client.patient.api = createAPI(fhir, {
            baseUrl: server.serviceUrl,
            patient: p.patientId,
            auth
        });
        client.patient.read = function(){
            return client.get({ resource: "Patient" });
        };
    }

    var fhirAPI = (client.patient)?client.patient.api:client.api;

    client.userId = p.userId;

    server.auth = server.auth || { type: "none" };

    if (!client.server.serviceUrl || !client.server.serviceUrl.match(/https?:\/\/.+[^/]$/)) {
        throw "Must supply a `server` property whose `serviceUrl` begins with http(s) " +
        "and does NOT include a trailing slash. E.g. `https://fhir.aws.af.cm/fhir`";
    }

    // =========================================================================
    

    /**
     * @param {Object|String} requestOptions Can be a string URL (relative to
     * the serviceUrl), or an object which will be passed to jQuery.ajax()
     * @param {Object} fhirOptions Additional options to control the behaviour
     * @param {Boolean} fhirOptions.useRefreshToken If false, the request will
     * fail if your access token is expired. If true (default), when you receive
     * a 401 response and you have a refresh token, the client will attempt to
     * re-authorize and try again.
     * @param {Number} fhirOptions.pageLimit When you request something that
     * returns a bundle and has multiple pages, the next (pageLimit - 1) pages
     * will also be requested and appended to the result. You can use it like so:
     * - fhirOptions.pageLimit = 1 - (default) Only get the current page
     * - fhirOptions.pageLimit = 3 - Get the first 3 pages
     * - fhirOptions.pageLimit = Infinity - Get all pages
     * @param {String|String[]} fhirOptions.resolveReferences One or more
     * references to resolve. 
     * @param {Boolean} fhirOptions.graph Ignored if `fhirOptions.resolveReferences`
     * is not used. If you use `fhirOptions.resolveReferences` and leave 
     * `fhirOptions.graph` as false, the result promise will be resolved with an
     * object like `{ data: Bundle, references: [ ...Resource ] }`.
     * If you set `fhirOptions.graph` to true, the resolved references will be
     * mounted in place and you just get the data property: `{ data: Bundle }`.
     */
    client.request = function(requestOptions, fhirOptions = {}, resolvedRefs = {}) {
        
        // requestOptions.url
        if (typeof requestOptions == "string") {
            requestOptions = { url: requestOptions };
        }
        requestOptions.url = absolute(requestOptions.url, server);

        // fhirOptions.resolveReferences
        if (!Array.isArray(fhirOptions.resolveReferences)) {
            fhirOptions.resolveReferences = [fhirOptions.resolveReferences];
        }
        fhirOptions.resolveReferences = fhirOptions.resolveReferences.filter(Boolean).map(String);

        // fhirOptions.graph
        fhirOptions.graph = (fhirOptions.graph !== false);

        // fhirOptions.pageLimit
        if (!fhirOptions.pageLimit && fhirOptions.pageLimit !== 0) {
            fhirOptions.pageLimit = 1;
        }

        const hasPageCallback = typeof fhirOptions.onPage == "function";

        return Adapter.get().http(requestOptions)
            .then(result => result.data)

            // Resolve Refernces
            .then(async (data) => {

                async function resolve(obj) {
                    for (let path of fhirOptions.resolveReferences) {
                        const ref = getPath(obj, path + ".reference");
                        if (ref) {
                            let sub = resolvedRefs[ref];
                            if (!sub) {
                                sub = await client.request(ref);
                                resolvedRefs[ref] = sub;
                            }


                            if (fhirOptions.graph) {
                                setPath(obj, path, sub);
                            }
                        }
                    }
                }

                if (data.resourceType == "Bundle") {
                    for (let item of (data.entry || [])) {
                        await resolve(item.resource);
                        // console.log(resource);
                    }
                } else {
                    await resolve(data);
                }

                return data;
            })

            // Pagination
            .then(async (data) => {
                if (data && data.resourceType == "Bundle") {
                    if (hasPageCallback) {
                        await fhirOptions.onPage(data, { ...resolvedRefs });
                    }

                    if (--fhirOptions.pageLimit) {
                        const links = data.link || [];
                        const next = links.find(l => l.relation == "next");
                        data = makeArray(data);
                        // console.log("===>", data);
                        if (next && next.url) {
                            const nextPage = await client.request(next.url, fhirOptions, resolvedRefs);

                            if (hasPageCallback) {
                                return null;
                            }
                            // console.log("===>", nextPage);
                            if (fhirOptions.resolveReferences.length) {
                                Object.assign(resolvedRefs, nextPage.references);
                                // console.log("===>", nextPage);
                                return data.concat(makeArray(nextPage.data || nextPage));
                            }
                            return data.concat(makeArray(nextPage));
                        }
                    }
                    
                }
                
                return data;
            })
            
            .then(data => {
                if (fhirOptions.graph) {
                    resolvedRefs = {};
                }
                else if (!hasPageCallback && fhirOptions.resolveReferences.length) {
                    return {
                        data,
                        references: resolvedRefs
                    };
                }
                return data;
            });
    };

    client.getPath = getPath;

    // =========================================================================

    client.authenticated = function(p) {
        if (server.auth.type === "none") {
            return p;
        }

        var h;
        if (server.auth.type === "basic") {
            h = "Basic " + btoa(server.auth.username + ":" + server.auth.password);
        } else if (server.auth.type === "bearer") {
            h = "Bearer " + server.auth.token;
        }
        if (!p.headers) {p.headers = {};}
        p.headers["Authorization"] = h;
        //p.beforeSend = function (xhr) { xhr.setRequestHeader ("Authorization", h); }

        return p;
    };

    

    client.get = function(p) {
        var ret = Adapter.get().defer();
        var params = { type: p.resource };

        if (p.id) {
            params["id"] = p.id;
        }

        fhirAPI.read(params).then(ret.resolve, ret.reject);
        return ret.promise;
    };

    client.user = {
        read() {
            var tokens = client.userId.split("/");
            return client.get({id: tokens.pop(), resource: tokens.pop()});
        }
    };

    client.getBinary = function(url) {

        var ret = Adapter.get().defer();

        Adapter.get().http(client.authenticated({
            type: "GET",
            url: url,
            dataType: "blob"
        })).done(function(blob){
            ret.resolve(blob);
        }).fail(function(){
            ret.reject("Could not fetch " + url, arguments);
        });
        return ret.promise;
    };

    client.fetchBinary = function(path) {
        var url = absolute(path, server);
        return client.getBinary(url);
    };

    return client;
}
